.button {
    display: flex;
    margin-top: 26px;
    margin-left: auto;
    margin-right: auto;
        width: 196px;

    
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        padding: 14px 28px;
    
        text-transform: uppercase;
    
    
        color: #373737;
    
        background: #EBD8FF;
        box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
        border-radius: 10.3108px;
        border: none;
}

.button:hover{
    background-color: #303f9f;
    color: white;
}
